*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

html {
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin-top: 0;
  padding: 0;
}

p, ul {
  margin-bottom: 0;
}

body {
  cursor: url("cursor.c4defcd5.png"), auto;
  font-family: Montserrat, sans-serif;
  font-style: normal;
}

body.modal-open, body.no-scroll, body.mobile-open {
  overflow: hidden;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

:root {
  --backcgoundSection: #aec6a5;
  --secondaryColor: #f8d24e;
  --textColorMain: #131313;
  --textColorMainAlt: #fcfcfc;
  --textColorSecondary: #d6936d;
  --textColorThird: #ababab;
  --textColorFooter: #7a7a7a;
  --backgroundColorAbout: #84a178;
  --headerHeroBackground: #84a278;
  --iconFill: #fff;
  --hoverIconFill: #0a8817;
  --blackColor: #000;
}

.header-container, .hero-container {
  width: 320px;
  height: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .header-container, .hero-container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .header-container, .hero-container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.header {
  width: 100%;
  background-color: var(--headerHeroBackground);
  padding-top: 12px;
}

@media screen and (min-width: 768px) {
  .header {
    padding-top: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .header {
    padding-top: 8px;
  }
}

.header-logo {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.header-logo:hover {
  cursor: url("yellow-cursor.d6420481.png"), auto;
  transform: scale(1.5);
}

.header-logo:focus {
  outline: none;
}

.navigation-list {
  display: none;
}

@media screen and (min-width: 1200px) {
  .navigation-list {
    align-items: baseline;
    gap: 28px;
    display: flex;
  }
}

.navigation-link {
  color: var(--textColorMainAlt);
  align-items: center;
  padding-top: 32px;
  padding-bottom: 13px;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.25;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.navigation-link:hover, .navigation-link:focus {
  color: var(--secondaryColor);
  cursor: url("yellow-cursor.d6420481.png"), auto;
  outline: none;
  transform: scale(1.2);
}

.menu-button {
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
  padding: 0;
}

.menu-button:hover, .menu-button:focus {
  cursor: url("yellow-cursor.d6420481.png"), auto;
}

@media screen and (min-width: 1200px) {
  .menu-button {
    display: none;
  }
}

.menu-icon {
  fill: var(--iconFill);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.menu-button:hover .menu-icon, .menu-button:focus .menu-icon {
  fill: var(--hoverIconFill);
  cursor: url("yellow-cursor.d6420481.png"), auto;
  outline: none;
}

@media screen and (min-width: 768px) {
  .menu-icon {
    margin-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .menu-icon {
    display: none;
  }
}

.header-buttons {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.mobile-menu {
