  width: 100vw;
  height: 100vh;
  background-color: var(--headerHeroBackground);
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.mobile-menu.is-open {
  transform: translateX(0);
}

.mob-header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.mob-container {
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  overflow: auto;
}

.mob-menu-top {
