.footer-section {
  background-color: #84a178;
}
.container {
  margin: 0 auto;
  max-width: 480px;
  @media screen and (min-width: 768px) {
    max-width: 768px;
  }
  @media screen and (min-width: 1200px) {
    padding: 0 16px;
    max-width: 1200px;
  }
}
.footer-wrap {
  width: 100%;
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    padding-bottom: 32px;
  }
  @media screen and (min-width: 1200px) {
    padding-bottom: 60px;
  }
}
.title-wrap {
  margin-bottom: 60px;
  padding-left: 20px;
  @media screen and (min-width: 768px) {
    padding-left: 32px;
    margin: 0;
  }
  @media screen and (min-width: 1200px) {
    padding-left: 0;
  }
}
.footer-title {
  font-weight: 700;
  color: var(--textColorMainAlt);
  font-size: 18px;
  line-height: 1.2222;
  max-width: 232px;
  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.2307;
    letter-spacing: 0.04em;
    max-width: 352px;
  }
  @media screen and (min-width: 1200px) {
    font-weight: 400;
    font-size: 30px;
    line-height: 1.2333;
    max-width: 100%;
  }
}
.footer-accent {
  display: block;
  font-size: 34px;
  line-height: 1.2058;
  letter-spacing: 0.04em;
  margin-top: 16px;
  max-width: 200px;
  @media screen and (min-width: 768px) {
    font-size: 48px;
    letter-spacing: 0.06em;
    margin-top: 8px;
    max-width: 352px;
  }
  @media screen and (min-width: 1200px) {
    font-weight: 700;
    font-size: 58px;
    letter-spacing: 1.2241;
    margin-top: 14px;
    max-width: 100%;
  }
}
.networks-list {
  display: flex;
  gap: 20px;
  justify-content: flex-end;
  margin-bottom: 20px;
  padding-right: 20px;
  @media screen and (min-width: 768px) {
    margin-bottom: 34px;
    margin-top: 61px;
    padding-right: 32px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 40px;
    margin-top: 18px;
    padding-right: 0;
  }
}
.networks-item {
  width: 34px;
  height: 34px;
}
.networks-link {
  display: flex;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  background-color: var(--secondaryColor);
  cursor: url('./../images/cursor.png');
}
.networks-link:hover {
    cursor: url('./../images/yellow-cursor.png'), auto;
}
.networks-link:focus {
    outline: none;
}
.networks-icon {
  fill: #84a178;
  transition-property: fill;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  cursor: url('./../images/cursor.png');
  .networks-link:hover & {
    fill: var(--textColorMainAlt);
  }
  .networks-link:focus & {
    fill: var(--textColorMainAlt);
  }
}

.footer-contact {
  text-align: end;
  margin-bottom: 40px;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.8333;
  color: var(--textColorMain);
  padding-right: 20px;
  @media screen and (min-width: 768px) {
    line-height: 1.8571;
    padding-right: 32px;
    margin-bottom: 0;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 1.875;
    letter-spacing: 0.02em;
    padding-right: 0;
  }
}
.footer-tel {
  color: var(--textColorMainAlt);
  transition-property: font-size;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  cursor: url('./../images/cursor.png'); 
  &:focus {
    outline: none;
  }
}
.footer-tel:hover {
  cursor: url('./../images/yellow-cursor.png'), auto;
  font-size: 14px;
}
.rights {
  color: var(--textColorFooter);
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.8571;
    letter-spacing: 0.02em;
  }
  @media screen and (min-width: 1200px) {
    font-size: 12px;
    line-height: 1.8333;
  }
}
.footer-border {
  align-items: center;
  justify-content: center;
  border-top: 1px solid #e1e1e1;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
  @media screen and (min-width: 768px) {
    padding-top: 12px;
    padding-bottom: 12px;
    display: flex;
    flex-wrap: nowrap;
  }
}
